/* eslint-disable jsx-a11y/no-autofocus, react/prop-types */
import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import {
  convertPropToCssClassName,
  aggregatedClasses,
} from '@elliemae/ds-classnames';

const CounterArea = aggregatedClasses('div')(
  'counter',
  'textarea',
  ({ limitError }) => ({
    'limit-error': limitError,
  }),
);

const DSLargeInputText = ({
  containerProps = {},
  autoFocus = false,
  className = '',
  disabled = false,
  name = '',
  id = null,
  maxLength = null,
  minLength = 0,
  placeholder = '',
  onClick = () => null,
  onChange = () => null,
  onFocus = () => null,
  onBlur = () => null,
  onKeyUp = () => null,
  onKeyDown = () => null,
  onPaste = () => null,
  value = '',
  hasError = false,
  required = false,
  readOnly = false,
  resize = 'vertical',
  innerRef = null,
  ariaLabel = 'Enter text',
  ariaAutocomplete = 'none',
  role = 'textbox',
  characterLimitCounter = 0,
  rows = 3,
  errorLimit = 'Please limit your entry to',
  tabIndex = '',
}) => {
  const limitError = useMemo(
    () =>
      characterLimitCounter < String(value).length && characterLimitCounter > 0,
    [characterLimitCounter, value],
  );
  const {
    cssClassName,
    classNameElement,
    classNameBlock,
  } = convertPropToCssClassName('form-element-textarea', 'text', {
    hasError: limitError || hasError,
    readOnly,
    disabled,
  });
  // auto size when user types
  const handleKeyUp = e => {
    // const { target } = e
    onKeyUp(e);
    // if (e.key !== 'Enter') return
    // target.style.cssText = 'height:auto; padding:0'
    // target.style.cssText = `height: ${target.scrollHeight}px`
  };
  return (
    <>
      <div {...containerProps} className={`${cssClassName} ${className}`}>
        <div className={classNameBlock('wrapper')}>
          <textarea
            ref={innerRef}
            aria-autocomplete={ariaAutocomplete}
            aria-label={ariaLabel}
            aria-multiline
            aria-placeholder={placeholder}
            aria-required={required}
            autoFocus={autoFocus}
            className={classNameElement('textarea')}
            data-testid="ds-large-input_main-component"
            disabled={disabled}
            id={id}
            maxLength={maxLength}
            minLength={minLength}
            name={name}
            onBlur={onBlur}
            onChange={onChange}
            onClick={onClick}
            onFocus={onFocus}
            onKeyDown={onKeyDown}
            onKeyUp={handleKeyUp}
            onPaste={onPaste}
            placeholder={placeholder}
            role={role}
            rows={String(rows)}
            style={{
              resize,
            }}
            value={value}
            tabIndex={tabIndex}
          />
        </div>
      </div>
      {characterLimitCounter > 0 && (
        <CounterArea classProps={{ limitError }} data-testid="counter-area">
          {`${
            limitError
              ? `${errorLimit} ${characterLimitCounter} characters.   `
              : ''
          }${String(value).length} / ${characterLimitCounter}`}
        </CounterArea>
      )}
    </>
  );
};

DSLargeInputText.propTypes = {
  containerProps: PropTypes.shape({}),
  /**
   * Whether uses auto focus or not
   */
  autoFocus: PropTypes.bool,
  className: PropTypes.string,
  /**
   * Whether is disabled or not
   */
  disabled: PropTypes.bool,
  name: PropTypes.string,
  id: PropTypes.number,
  /**
   * Max length for text to be entered
   */
  // eslint-disable-next-line max-lines
  maxLength: PropTypes.number,
  /**
   * Min length for text to be entered
   */
  minLength: PropTypes.number,
  /**
   * Placeholder for the text
   */
  placeholder: PropTypes.string,
  /**
   * Allows a function that is triggered once the input text is clicked
   */
  onClick: PropTypes.func,
  /**
   * Allows a function that is triggered once the input text changes
   */
  onChange: PropTypes.func,
  /**
   * Allows a function that is triggered once the input text is focused
   */
  onFocus: PropTypes.func,
  /**
   * Allows a function that is triggered once the input text loses focus
   */
  onBlur: PropTypes.func,
  /**
   * Allows a function that is triggered once in the input a keyboard key is released
   */
  onKeyUp: PropTypes.func,
  /**
   * Allows a function that is triggered once a key is being pressed
   */
  onKeyDown: PropTypes.func,
  /**
   * Allows a function that is triggered once the input text is pasted
   */
  onPaste: PropTypes.func,
  /**
   * Default value once the component is initialized
   */
  value: PropTypes.string,
  /**
   * Whether the input text has error or not
   */
  hasError: PropTypes.bool,
  /**
   * Whether the input text is required or not
   */
  required: PropTypes.bool,
  /**
   * Whether the input text is read only or not
   */
  readOnly: PropTypes.bool,
  /**
   * Whether the input text style is vertical or not
   */
  resize: PropTypes.string,
  innerRef: PropTypes.func,
  ariaLabel: PropTypes.string,
  ariaAutocomplete: PropTypes.string,
  /**
   * Type of input. Ex: textbox
   */
  role: PropTypes.string,
  /**
   * it will show a counter/limit if the value is greater than 0
   */
  characterLimitCounter: PropTypes.number,
  /**
   * Tab index html property.
   */
  tabIndex: PropTypes.string,
};

export default DSLargeInputText;
